
import { mapState } from 'vuex'
import { AMapManager } from 'vue-amap'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'

import { TMap } from '../utils/TMap'
import nearShop from '../components/goods/nearShop'
export default {
directives: {
TransferDom
},
components: {
Popup,
nearShop
},
name: 'tmap',
data () {
return {
zoom: 12,
center: [25.049525, 102.702599],
centerMarket: null,
map: null,
qq: null,
drivingService: null,
markerList: [],
local: [],
nearShop: {},
show8: true,
f: false,
address: ''
}
},
computed: {
...mapState({
openid: state => state.vux.openid,
}),
juli: function(){
var juli = 0;
if(this.thisrouter.distance) juli = this.thisrouter.distance / 1000
return juli.toFixed(2)
},
time: function(){
var time = 0;
if(this.thisrouter.distance) time = this.thisrouter.time / 60
return time.toFixed(0)
}
},
activated: function () {
var self = this
this.show8 = true
},
created: function() {
var self = this
self.createMap()

},
beforeUpdate: function(){
this.setWxShare('','','http://xcxcgj.gotomore.cn/index.html#/tmap','http://xcxcgj.gotomore.cn/public/xarlogo.jpg');
},
methods: {
createMap: function (){
var self = this
TMap('JAABZ-XNT3O-QP6WO-SFXYC-2XWBO-3KF6G').then(qq => {
self.qq = qq
var center = new self.qq.maps.LatLng(self.center[0], self.center[1])
self.map = new self.qq.maps.Map(document.getElementById("container"), {

center: center,
zoom: self.zoom,
mapTypeControlOptions: {
mapTypeIds: [
],
position: qq.maps.ControlPosition.TOP_RIGHT
},
zoomControl: false,
});




self.fetchData()

self.location()
});
},
addMarker: function (lat, lng, icon = '', cb = null, add = true) {
var self = this
var marker = null
if(lat && lng && self.qq){
marker = new self.qq.maps.Marker({

position: new self.qq.maps.LatLng(lat, lng),

map: self.map,
});
marker.setAnimation(self.qq.maps.MarkerAnimation.DOWN);
var size = new self.qq.maps.Size(42, 48),
origin = null,
anchor = null
if(icon){
var i = new self.qq.maps.MarkerImage(
icon,
size,
origin,
anchor,
size
);
marker.setIcon(i);
}
self.qq.maps.event.addListener(marker, 'click', function() {

if(typeof(cb) == 'function') cb(lat, lng)
});
if(add) self.markerList.push(marker)
}
return marker
},
deleteMarker: function () {
this.markerList.map(function(item){
item.setMap(null)
})
this.markerList = []
},
fetchData: function () {
var self = this
self.resetMap()
this.xarpost('Shopinfo/shopList', {}).then(function(d){
let {data, error, message} = d
var min_juli = 0;
var min_juli_index = 0;
self.deleteMarker()
data.map(function(item, index){
var juli = self.distance(self.center, [item.shop_lat, item.shop_lng])
if(index == 0) min_juli = juli
if(juli < min_juli ){
min_juli = juli
min_juli_index = index
}
item.juli = juli
})
data.map(function(item, index){
self.show8 = false
var icon = '../../static/image/WechatIMG4.png'
if(min_juli_index == index){
icon = '../../static/image/WechatIMG4.png'
}
self.addMarker(item.shop_lat, item.shop_lng, icon, function(lat, lng){

self.nearShop = item

self.show8 = true
})
})
self.shop = data
},function(e){
console.log(e)
})
},
driving: function(latlng = [], latlng1 = []){
var self = this
var start = new self.qq.maps.LatLng(latlng[0], latlng[1]);
var end = new self.qq.maps.LatLng(latlng1[0], latlng1[1]);

self.drivingService.clear()
self.drivingService.setPolicy(self.qq.maps.DrivingPolicy['LEAST_TIME']);
self.drivingService.search(start, end);
self.drivingService.setComplete(function(result) {
console.log(result)
if (result.type == self.qq.maps.ServiceResultType.MULTI_DESTINATION) {

var d = result.detail;
}
});
self.drivingService.setComplete(function(result) {
console.log(result)
});



},
distance: function(latlng = [], latlng1 = []){

var juli = 0
var self = this
if(latlng && latlng1){
console.log(latlng, latlng1)
var start = new self.qq.maps.LatLng(latlng[0], latlng[1]);
var end = new self.qq.maps.LatLng(latlng1[0], latlng1[1]);
juli = self.qq.maps.geometry.spherical.computeDistanceBetween(start, end)
}
return juli
},
getAddress: function (lat, lng) {
var geocoder, map, marker = null;
geocoder = new this.qq.maps.Geocoder();
var latLng = new this.qq.maps.LatLng(lat, lng);

geocoder.getAddress(latLng);

var self = this
geocoder.setComplete(function(result) {
console.log(result)
self.address = result.detail.address
});

geocoder.setError(function() {
alert("");
});
},
handler(index) {
alert(`${ index } - trigger`);
},
location(){
var self = this
self.resetMap()
if(self.centerMarket) self.centerMarket.setMap(null)
self.getLocation(false).then(function(res){
self.center = [res.latitude, res.longitude]

self.map.panTo(new self.qq.maps.LatLng(self.center[0], self.center[1]));
self.getAddress(res.latitude, res.longitude)
self.centerMarket = self.addMarker(self.center[0], self.center[1], '../../static/image/positioning.svg', null, false)
self.fetchData()
}, function(e){
self.map.panTo(new self.qq.maps.LatLng(self.center[0], self.center[1]));
self.getAddress(self.center[0], self.center[1])
self.centerMarket = self.addMarker(self.center[0], self.center[1], '../../static/image/positioning.svg', null, false)
self.fetchData()
});
},
resetMap(){
var self = this
if(self.map) self.map.setZoom(self.zoom)
if(self.drivingService) self.drivingService.clear()
}
}
}



